.reviews {
  text-align: center;
  margin: 40px 0;

  @media (min-width: $tablet-width) {
    margin: 84px 0 13px;
  }

  @media (min-width: $desktop-width) {
    margin: 170px 0 9px;
  }
}

.reviews__prev,
.reviews__next {
  display: none;

  @media (min-width: $desktop-width) {
    @include button-reset;
    display: block;
    position: absolute;
  }
}

.reviews__prev {
  top: 20%;
  left: 0;

  &::before {
    position: absolute;
    content: "";
    width: 22px;
    height: 41px;
    background-image: url("../img/arrow.svg");
    background-repeat: no-repeat;
  }
}

.reviews__next {
  top: 20%;
  right: 0;

  &::after {
    position: absolute;
    content: "";
    width: 22px;
    height: 41px;
    top: 20%;
    right: 0;
    background-image: url("../img/arrow.svg");
    background-repeat: no-repeat;
    transform: scale(-1, 1);
  }
}

.reviews__switch {
  @media (min-width: $tablet-width) {
    margin-top: 24px;
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.reviews__list {
  @media (min-width: $desktop-width) {
    padding: 0 75px 20px;
    position: relative;
  }
}
